@font-face {
  font-family: "Averta Demo PE Cutted Demo";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.astudio__oh {
  margin-top: 117px;
  text-align: center;
}
.astudio__oh h2 {
  font-family: "Averta Demo PE Cutted Demo";
  font-weight: 400;
  font-size: 40px;
  line-height: 49.44px;
  color: #000000;
  margin: 0;
}
.astudio__oh p {
  font-family: "Averta Demo PE Cutted Demo";
  font-weight: 400;
  font-family: 18px;
  line-height: 28px;
  color: #757575;
  margin: 17px 0 0 0;
}
@media (min-width: 0) and (max-width: 768px) {
  .astudio__oh {
    margin: 50px 20px 0;
  }
}